.container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100vh;
  }
  
  .heading {
    font-size: 3rem;
    font-weight: bold;
    margin-bottom: 1rem;
  }
  
  .text {
    font-size: 1.2rem;
    margin-bottom: 2rem;
    color: white;
  }
  
  .button {
    padding: 1rem 2rem;
    font-size: 1rem;
    font-weight: bold;
    color: white;
    background-color: #007bff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.1);
    transition: background-color 0.3s ease-in-out;
  }
  
  .button:hover {
    background-color: #091e33;
  }
  