.modal {
  position: fixed;
  z-index: 1;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: rgb(0, 0, 0);
  background-color: rgba(0, 0, 0, 0.4);
}
.notification {
  background-color: #bf360c;
  border: 1px solid #e0e0e0;
  border-radius: 90px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  padding: 10px 10px;
  display: flex;
  flex-direction: column;
  margin: 12% 31%;
  justify-content: center;
  align-items: center;
  color: white;
  padding-bottom: 30px;
}
.errorIcon {
  margin-right: 8px;
}
.errorMessage {
  margin: 0;
}
.returnButton {
  background-color: purple;
  color: #ffffff;
  border: none;
  border-radius: 90px;
  cursor: pointer;
  transition: background-color 0.3s ease-in-out;
  margin-top: 25px;
  font-size: 15px;
  padding: 10px 125px;
}

.returnButton:hover {
  background-color: #0056b3;
}
